/* eslint-disable react-hooks/exhaustive-deps */
import React, { useEffect } from "react";
import "./keyboard.scss";
import { useDispatch, useSelector } from "react-redux";
import Button from "../../component/button/button";
import { handleFunctionToCallOnKeyPress } from "./helpers/handleFunctionToCallOnKeyPress";
import { keyboard } from "./helpers/keyboardArray";

const Keyboard = () => {
  const dispatch = useDispatch();
  const calculator = useSelector((state) => state.calculator);

  useEffect(() => {
    document.addEventListener("keypress", (event) => {
      handleFunctionToCallOnKeyPress(event, calculator, dispatch)
    }
    );
    return document.removeEventListener("keypress", (event) =>
      handleFunctionToCallOnKeyPress(event, calculator, dispatch)
    );
  }, [calculator]);

  return (
    <section className="keyboard">
      {keyboard(calculator).map((button) => {
        return (
          <Button
            key={button.value}
            value={button.value}
            onclick={button.function}
          />
        );
      })}
    </section>
  );
};

export default Keyboard;
