import { convertStringToNumber } from "./convertStringToNumber";
import { convertNumberToString } from "./convertNumberToString";

export const calculatePower = ({
  historyOfOperationsArray,
  mainValueString,
  isEqualSignUsed,
  isSignChanged,
}) => {
  if (isEqualSignUsed || historyOfOperationsArray.length < 1) {
    //  "=" used or empty array
    return {
      bigString: convertNumberToString(
        Math.pow(convertStringToNumber(mainValueString), 2)
      ),
      smallArray: [
        convertNumberToString(
          Math.pow(convertStringToNumber(mainValueString), 2)
        ),
      ],
    };
  } else if (historyOfOperationsArray.length > 0) {
    //  it's not a empty array
    if (
      isNaN(
        convertStringToNumber(
          historyOfOperationsArray.slice(-1).join("")
        )
      )
    ) {
      //  last character isn't number
      return {
        bigString: convertNumberToString(
          Math.pow(convertStringToNumber(mainValueString), 2)
        ),
        smallArray: [
          ...historyOfOperationsArray,
          convertNumberToString(
            Math.pow(convertStringToNumber(mainValueString), 2)
          ),
        ],
      };
    } else {
      //  last character is number
      if (isSignChanged) {
        return {
          bigString: convertNumberToString(
            Math.pow(convertStringToNumber(mainValueString), 2)
          ),

          smallArray: [
            convertNumberToString(
              Math.pow(convertStringToNumber(mainValueString), 2)
            ),
          ],
        };
      } else {
        const temp = historyOfOperationsArray.map((element, index) =>
          index !== historyOfOperationsArray.length - 1
            ? element
            : convertNumberToString(Math.pow(convertStringToNumber(element), 2))
        );
        return {
          bigString: temp.slice(-1).join(""),
          smallArray: temp,
        };
      }
    }
  }
};
