import { convertStringToNumber } from "./convertStringToNumber";
import { convertNumberToString } from "./convertNumberToString";

export const calculateSquareRoot = ({
  historyOfOperationsArray,
  mainValueString,
  isEqualSignUsed,
  isSignChanged,
}) => {
  if (isEqualSignUsed || historyOfOperationsArray.length < 1) {
    //  "=" used or empty array
    if (convertStringToNumber(mainValueString) >= 0) {
      return {
        bigString: convertNumberToString(
          Math.sqrt(convertStringToNumber(mainValueString))
        ),
        smallArray: [
          convertNumberToString(
            Math.sqrt(convertStringToNumber(mainValueString))
          ),
        ],
        error: false,
      };
    } else {
      return {
        bigString: "Invalid input",
        smallArray: ["√(" + mainValueString + ")"],
        error: true,
      };
    }
  } else if (historyOfOperationsArray.length > 0) {
    //  it's not a empty array
    if (
      isNaN(
        convertStringToNumber(
          historyOfOperationsArray.slice(-1).join("")
        )
      )
    ) {
      //  last character isn't number
      if (convertStringToNumber(mainValueString) >= 0) {
        return {
          bigString: convertNumberToString(
            Math.sqrt(convertStringToNumber(mainValueString))
          ),
          smallArray: [
            ...historyOfOperationsArray,
            convertNumberToString(
              Math.sqrt(convertStringToNumber(mainValueString))
            ),
          ],
          error: false,
        };
      } else {
        return {
          bigString: "Invalid input",
          smallArray: [
            ...historyOfOperationsArray,
            "√(" + mainValueString + ")",
          ],
          error: true,
        };
      }
    } else {
      //  last character is number
      if (isSignChanged) {
        if (convertStringToNumber(mainValueString) >= 0) {
          return {
            bigString: convertNumberToString(
              Math.sqrt(convertStringToNumber(mainValueString))
            ),

            smallArray: [
              convertNumberToString(
                Math.sqrt(convertStringToNumber(mainValueString))
              ),
            ],
            error: false,
          };
        } else {
          return {
            bigString: "Invalid input",
            smallArray: [
              ...historyOfOperationsArray,
              "√(" + mainValueString + ")",
            ],
            error: true,
          };
        }
      } else {
        if (
          convertStringToNumber(
            historyOfOperationsArray.slice(-1).join("")
          ) >= 0
        ) {
          const temp = historyOfOperationsArray.map((element, index) =>
            index !== historyOfOperationsArray.length - 1
              ? element
              : convertNumberToString(Math.sqrt(convertStringToNumber(element)))
          );
          return {
            bigString: temp.slice(-1).join(""),
            smallArray: temp,
            error: false,
          };
        } else {
          return {
            bigString: "Invalid input",
            smallArray: historyOfOperationsArray.map(
              (element, index) =>
                index !== historyOfOperationsArray.length - 1
                  ? element
                  : "√(" + element + ")"
            ),
            error: true,
          };
        }
      }
    }
  }
};
