import React from "react";
import { useSelector } from "react-redux";
import "./display.scss";
import { formatBigDisplay } from "./helpers/formatBigDisplay";
import { formatSmallDisplay } from "./helpers/formatSmallDisplay";

const Display = () => {
  const calculator = useSelector((state) => state.calculator);

  return (
    <section className="display">
      <p className="display__small">
        {formatSmallDisplay(
          calculator.historyOfOperationsArray.join(" ")
        )}
      </p>
      <p className="display__big">
        {formatBigDisplay(calculator.mainValueString)}
      </p>
    </section>
  );
};

export default Display;
