import { convertNumberToString } from "./convertNumberToString";
import { convertStringToNumber } from "./convertStringToNumber";
import { calculateResult } from "./calculateResult";

export const calculatePercent = ({
  historyOfOperationsArray,
  mainValueString,
  isEqualSignUsed,
  result,
}) => {
  const mainValueNumber = convertStringToNumber(mainValueString);
  const lastFromSmallArrayNumber = convertStringToNumber(
    historyOfOperationsArray.slice(-1).join("")
  );

  if (historyOfOperationsArray.length < 1) {
    return {
      bigString: "0",
      smallArray: ["0"],
    };
  }

  if (result) {
    const calculatedString = convertNumberToString(
      mainValueNumber * (mainValueNumber / 100)
    );
    return {
      bigString: calculatedString,
      smallArray: isEqualSignUsed
        ? [calculatedString]
        : [...historyOfOperationsArray, calculatedString],
    };
  }

  const prevResult = isNaN(lastFromSmallArrayNumber)
    ? calculateResult(historyOfOperationsArray.slice(0, -1)).string
    : calculateResult(historyOfOperationsArray.slice(0, -2)).string;

  const calculatedString = convertNumberToString(
    convertStringToNumber(prevResult) * (mainValueNumber / 100)
  );

  return {
    bigString: calculatedString,
    smallArray: isNaN(lastFromSmallArrayNumber)
      ? [...historyOfOperationsArray, calculatedString]
      : historyOfOperationsArray.map((element, index) =>
          index !== historyOfOperationsArray.length - 1
            ? element
            : calculatedString
        ),
  };
};
