import * as calculatorActions from "../../../actions/calculatorActions";

export const handleFunctionToCallOnKeyPress = (event, state, dispatch) => {
  event.preventDefault();
  switch (event.keyCode) {
    //    1
    case 49:
      dispatch(calculatorActions.mainValueChanged(1, state));
      break;

    //    2
    case 50:
      dispatch(calculatorActions.mainValueChanged(2, state));
      break;

    //    3
    case 51:
      dispatch(calculatorActions.mainValueChanged(3, state));
      break;
    //    4
    case 52:
      dispatch(calculatorActions.mainValueChanged(4, state));
      break;

    //    5
    case 53:
      dispatch(calculatorActions.mainValueChanged(5, state));
      break;

    //    6
    case 54:
      dispatch(calculatorActions.mainValueChanged(6, state));
      break;

    //    7
    case 55:
      dispatch(calculatorActions.mainValueChanged(7, state));
      break;

    //    8
    case 56:
      dispatch(calculatorActions.mainValueChanged(8, state));
      break;

    //    9
    case 57:
      dispatch(calculatorActions.mainValueChanged(9, state));
      break;

    //    0
    case 48:
      dispatch(calculatorActions.mainValueChanged(0, state));
      break;

    //    ,
    case 44:
      dispatch(calculatorActions.mainValueChanged(",", state));
      break;

    //    +
    case 43:
      dispatch(calculatorActions.operationDone("+", state));
      break;

    //    -
    case 45:
      dispatch(calculatorActions.operationDone("-", state));
      break;

    //    *
    case 42:
      dispatch(calculatorActions.operationDone("×", state));
      break;
    //    /
    case 47:
      dispatch(calculatorActions.operationDone("/", state));
      break;
    //    enter
    case 13:
      dispatch(calculatorActions.operationDone("=", state));
      break;
    //    =
    case 61:
      dispatch(calculatorActions.operationDone("=", state));
      break;
    //    backspace
    case 8:
      dispatch(calculatorActions.lastCharacterDeleted(state));
      break;

    //    esc (C)
    case 27:
      dispatch(calculatorActions.allCleared());
      break;

    //    delete (CE)
    case 46:
      dispatch(calculatorActions.mainValueCleared());
      break;

    default:
      break;
  }
};
