import { verifyNumberOnChange } from "../helpers/verifyNumberOnChange";
import { createNewHistoryOfOperationsObject } from "../helpers/createNewHistoryOfOperationsObject";
import { calculateResult } from "../helpers/calculateResult";
import { calculatePower } from "../helpers/calculatePower";
import { calculateSquareRoot } from "../helpers/calculateSquareRoot";
import { divideOneByX } from "../helpers/divideOneByX";
import { calculatePercent } from "../helpers/calculatePercent";
import * as deleteLastCharacter from "../helpers/deleteLastCharacter";
import { changeSign } from "../helpers/changeSign";

export const mainValueChanged = (number, state) => ({
  type: "mainValueChanged",
  payload: {
    number,
    mainValueString: verifyNumberOnChange(state, number),
  },
});

export const mainValueCleared = () => ({
  type: "mainValueCleared",
});

export const allCleared = () => ({
  type: "allCleared",
});

export const operationDone = (operation, state) => ({
  type: "operationDone",
  payload: {
    operation,
    newSmallDisplayValuesObjectArrayWithError: createNewHistoryOfOperationsObject(
      operation,
      state
    ),
    calculatedResultAndErrorObject: calculateResult(
      createNewHistoryOfOperationsObject(operation, state)
    ),
  },
});

export const lastCharacterDeleted = (state) => ({
  type: "lastCharacterDeleted",
  payload: {
    mainValueString: state.isError
      ? "0"
      : deleteLastCharacter.deleteLastCharacterFromMainDisplay(state),
    historyOfOperationsArray: state.isError
      ? []
      : deleteLastCharacter.deleteHistoryOfOperations(state),
  },
});

export const signChanged = (state) => ({
  type: "signChanged",
  payload: {
    signChangedObject: changeSign(state),
  },
});

export const powerCalculated = (state) => ({
  type: "powerCalculated",
  payload: {
    calculatedPowerObject: calculatePower(state),
  },
});

export const squareRootCalculated = (state) => ({
  type: "squareRootCalculated",
  payload: {
    calculatedSquareRootObject: calculateSquareRoot(state),
  },
});

export const oneByXDivided = (state) => ({
  type: "oneByXDivided",
  payload: {
    oneByXDividedObject: divideOneByX(state),
  },
});

export const percentCalculated = (state) => ({
  type: "percentCalculated",
  payload: {
    percentCalculatedObject: calculatePercent(state),
  },
});
