import * as calculatorActions from "../../../actions/calculatorActions";

export const keyboard = (state) => {
  return [
    {
      value: "%",
      function: calculatorActions.percentCalculated(state),
    },
    {
      value: "CE",
      function: calculatorActions.mainValueCleared(),
    },
    {
      value: "C",
      function: calculatorActions.allCleared(),
    },
    {
      value: "⊲",
      function: calculatorActions.lastCharacterDeleted(state),
    },
    {
      value: "1/x",
      function: calculatorActions.oneByXDivided(state),
    },
    {
      value: "x²",
      function: calculatorActions.powerCalculated(state),
    },
    {
      value: "√x",
      function: calculatorActions.squareRootCalculated(state),
    },
    {
      value: "÷",
      function: calculatorActions.operationDone("÷", state),
    },
    {
      value: "7",
      function: calculatorActions.mainValueChanged(7, state),
    },
    {
      value: "8",
      function: calculatorActions.mainValueChanged(8, state),
    },
    {
      value: "9",
      function: calculatorActions.mainValueChanged(9, state),
    },
    {
      value: "×",
      function: calculatorActions.operationDone("×", state),
    },
    {
      value: "4",
      function: calculatorActions.mainValueChanged(4, state),
    },
    {
      value: "5",
      function: calculatorActions.mainValueChanged(5, state),
    },
    {
      value: "6",
      function: calculatorActions.mainValueChanged(6, state),
    },
    {
      value: "-",
      function: calculatorActions.operationDone("-", state),
    },
    {
      value: "1",
      function: calculatorActions.mainValueChanged(1, state),
    },
    {
      value: "2",
      function: calculatorActions.mainValueChanged(2, state),
    },
    {
      value: "3",
      function: calculatorActions.mainValueChanged(3, state),
    },
    {
      value: "+",
      function: calculatorActions.operationDone("+", state),
    },
    {
      value: "+/-",
      function: calculatorActions.signChanged(state),
    },
    {
      value: "0",
      function: calculatorActions.mainValueChanged(0, state),
    },
    {
      value: ",",
      function: calculatorActions.mainValueChanged(",", state),
    },
    {
      value: "=",
      function: calculatorActions.operationDone("=", state),
    },
  ];
};
