export const verifyNumberOnChange = (state, newNumber) => {
  if (state.isError) {
    return "" + newNumber;
  } else {
    if (
      (state.mainValueString.length < 17 &&
        state.mainValueString.indexOf(",") !== -1) ||
      (state.mainValueString.length < 16 &&
        state.mainValueString.indexOf(",") === -1) ||
      state.result
    ) {
      if (state.isEqualSignUsed || state.result) {
        //  clear big display
        if (newNumber === ",") {
          return "0,";
        }
        return "" + newNumber;
      } else {
        //  add next sign
        if (state.mainValueString.indexOf(",") !== -1 && newNumber === ",")
          return state.mainValueString;
        if (state.mainValueString === "0") {
          if (newNumber === ",") {
            return "0,";
          } else {
            return "" + newNumber;
          }
        }
        return state.mainValueString + newNumber;
      }
    } else {
      //  Cannot be longer
      return state.mainValueString;
    }
  }
};
