import React from "react";
import { useSelector, useDispatch } from "react-redux";
import "./button.scss";
import { buttonsToDisableArray } from "./consts/buttonsToDisableArray";
import { buttonClassName } from "./helpers/createClassName";
import { buttonOnClick } from "./helpers/createOnClick";

const Button = ({ value, onclick }) => {
  const calculator = useSelector((state) => state.calculator);
  const dispatch = useDispatch();
  const buttonOnClickValue = buttonOnClick(
    value,
    calculator.isError,
    buttonsToDisableArray
  )
    ? () => dispatch(onclick)
    : null;

  return (
    <button
      className={buttonClassName(
        value,
        calculator.isError,
        buttonsToDisableArray
      )}
      onClick={buttonOnClickValue}
    >
      {value}
    </button>
  );
};

export default Button;
