import { convertStringToNumber } from "./convertStringToNumber";

export const createNewHistoryOfOperationsObject = (
  operation,
  {
    historyOfOperationsArray,
    mainValueString,
    result,
    isEqualSignUsed,
    lastNumberAndOperationString,
    isError,
    isSignChanged,
  }
) => {
  if (isError) {
    return [];
  } else {
    if (isEqualSignUsed) {
      if (operation === "=") {
        //  =
        return [
          mainValueString,
          ...lastNumberAndOperationString.split(" "),
          operation,
        ];
      } else {
        //  +, -, *, /
        return [
          mainValueString.slice(-1) !== ","
            ? mainValueString
            : mainValueString + "0",
          operation,
        ];
      }
    } else {
      //  !isEqualSignUsed
      if (result) {
        if (
          !isNaN(
            convertStringToNumber(
              historyOfOperationsArray.slice(-1).join("")
            )
          )
        ) {
          //  number
          if (isSignChanged) {
            return [mainValueString, operation];
          }
          return [...historyOfOperationsArray, operation];
        } else if (operation !== "=") {
          //  +, -, *, /
          return historyOfOperationsArray.map((element, index) =>
            index !== historyOfOperationsArray.length - 1
              ? element
              : operation
          );
        } else {
          //  =
          return [
            ...historyOfOperationsArray,
            mainValueString,
            operation,
          ];
        }
      } else {
        //  !result
        if (
          !isNaN(
            convertStringToNumber(
              historyOfOperationsArray.slice(-1).join("")
            )
          )
        ) {
          return [...historyOfOperationsArray, operation];
        } else {
          return [
            ...historyOfOperationsArray,
            mainValueString.slice(-1) !== ","
              ? mainValueString
              : mainValueString + "0",
            operation,
          ];
        }
      }
    }
  }
};
