export default function calculateReducer(
  state = {
    mainValueString: "0",
    historyOfOperationsArray: [],
    lastNumberAndOperationString: "",
    result: false,
    isEqualSignUsed: false,
    isError: false,
    isSignChanged: false,
  },
  action
) {
  switch (action.type) {
    //  1, 2, 3, 4, 5, 6, 7, 8, 9, 0, "," --- done
    case "mainValueChanged":
      return {
        ...state,
        mainValueString: action.payload.mainValueString,
        historyOfOperationsArray:
          state.isError || state.isEqualSignUsed
            ? []
            : state.historyOfOperationsArray,
        result: false,
        isEqualSignUsed: false,
        isError: false,
        isSignChanged: false,
      };

    //  CE --- done
    case "mainValueCleared":
      return {
        ...state,
        mainValueString: "0",
        historyOfOperationsArray:
          state.isError || state.isEqualSignUsed
            ? []
            : state.historyOfOperationsArray,
        result: false,
        isEqualSignUsed: false,
        lastNumberAndOperationString: state.isEqualSignUsed
          ? ""
          : state.lastNumberAndOperationString,
        isError: false,
        isSignChanged: false,
      };

    //  C --- done
    case "allCleared":
      return {
        ...state,
        mainValueString: "0",
        historyOfOperationsArray: [],
        lastNumberAndOperationString: "",
        result: false,
        isEqualSignUsed: false,
        isError: false,
        isSignChanged: false,
      };

    //  +, -, *, /, = --- done
    case "operationDone":
      return {
        ...state,
        mainValueString: action.payload.calculatedResultAndErrorObject.string,
        historyOfOperationsArray:
          action.payload.newSmallDisplayValuesObjectArrayWithError,
        result:
          action.payload.operation === "=" && state.isError ? false : true,
        isEqualSignUsed:
          action.payload.operation === "=" && !state.isError ? true : false,
        lastNumberAndOperationString: action.payload.newSmallDisplayValuesObjectArrayWithError
          .slice(-3, -1)
          .join(" "),
        isError: action.payload.calculatedResultAndErrorObject.error,
        isSignChanged: false,
      };

    //  sqr --- done
    case "powerCalculated":
      return {
        ...state,
        mainValueString: action.payload.calculatedPowerObject.bigString,
        historyOfOperationsArray:
          action.payload.calculatedPowerObject.smallArray,
      };

    //  sqrt --- done
    case "squareRootCalculated":
      return {
        ...state,
        mainValueString: action.payload.calculatedSquareRootObject.bigString,
        historyOfOperationsArray:
          action.payload.calculatedSquareRootObject.smallArray,
        isError: action.payload.calculatedSquareRootObject.error,
      };

    //  1/x --- done
    case "oneByXDivided":
      return {
        ...state,
        mainValueString: action.payload.oneByXDividedObject.bigString,
        historyOfOperationsArray: action.payload.oneByXDividedObject.smallArray,
        isError: action.payload.oneByXDividedObject.error,
      };

    //  % --- done
    case "percentCalculated":
      return {
        ...state,
        mainValueString: action.payload.percentCalculatedObject.bigString,
        historyOfOperationsArray:
          action.payload.percentCalculatedObject.smallArray,
      };

    //  backspace --- done
    case "lastCharacterDeleted":
      return {
        ...state,
        mainValueString: action.payload.mainValueString,
        historyOfOperationsArray: action.payload.historyOfOperationsArray,
        isError: false,
        isSignChanged: false,
      };

    // +/- --- done
    case "signChanged":
      return {
        ...state,
        mainValueString: action.payload.signChangedObject.bigString,
        historyOfOperationsArray:
          action.payload.signChangedObject.smallArray !== null
            ? action.payload.signChangedObject.smallArray
            : state.historyOfOperationsArray,
        isSignChanged: action.payload.signChangedObject.isSignChanged,
      };

    default:
      return state;
  }
}
