export const deleteLastCharacterFromMainDisplay = ({
  mainValueString,
  result,
}) => {
  if (!result) {
    if (mainValueString.length > 1) {
      return mainValueString.slice(0, -1);
    } else {
      return "0";
    }
  } else {
    return mainValueString;
  }
};

export const deleteHistoryOfOperations = ({
  historyOfOperationsArray,
  isEqualSignUsed,
}) => {
  if (isEqualSignUsed) {
    return [];
  } else {
    return historyOfOperationsArray;
  }
};
