import { convertStringToNumber } from "./convertStringToNumber";
import { convertNumberToString } from "./convertNumberToString";

export const changeSign = ({
  mainValueString,
  historyOfOperationsArray,
}) => {
  if (
    !isNaN(
      convertStringToNumber(
        historyOfOperationsArray.slice(-1).join("")
      )
    )
  ) {
    return {
      bigString: convertNumberToString(
        -1 *
          convertStringToNumber(
            historyOfOperationsArray.slice(-1).join("")
          )
      ),
      smallArray: historyOfOperationsArray.map((element, index) =>
        index !== historyOfOperationsArray.length - 1
          ? element
          : convertNumberToString(
              -1 *
                convertStringToNumber(
                  historyOfOperationsArray.slice(-1).join("")
                )
            )
      ),
      isSignChanged: true,
    };
  } else {
    if (convertStringToNumber(mainValueString) !== 0) {
      return {
        bigString: convertNumberToString(
          -1 * convertStringToNumber(mainValueString)
        ),
        smallArray: null,
        isSignChanged: true,
      };
    } else {
      return { bigString: mainValueString, isSignChanged: false };
    }
  }
};
