import { convertStringToNumber } from "./convertStringToNumber";
import { convertNumberToString } from "./convertNumberToString";

export const divideOneByX = ({
  historyOfOperationsArray,
  mainValueString,
  isEqualSignUsed,
}) => {
  if (convertStringToNumber(mainValueString) !== 0) {
    if (!isEqualSignUsed) {
      return {
        bigString: convertNumberToString(
          1 / convertStringToNumber(mainValueString)
        ),
        smallArray: [
          ...historyOfOperationsArray,
          convertNumberToString(1 / convertStringToNumber(mainValueString)),
        ],
        error: false,
      };
    } else {
      return {
        bigString: convertNumberToString(
          1 / convertStringToNumber(mainValueString)
        ),
        smallArray: [
          convertNumberToString(1 / convertStringToNumber(mainValueString)),
        ],
        error: false,
      };
    }
  } else {
    return {
      bigString: "Do not divide by zero",
      smallArray: [...historyOfOperationsArray, "1/(0)"],
      error: true,
    };
  }
};
